import React, { PureComponent } from 'react';
import '../css/MainButtons.css';

export class MainButton extends PureComponent {

    render() {
        return (
            <div style={{top: "50%", left: "50%", transform: "translate(-50%, -50%)", position: "absolute"}}>
                <div style={{ display: "flex", alignItems: "center", justifyContent: "center", color: "white" }}>{this.props.icon}</div>
                <div style={{ display: "flex", alignItems: "center", justifyContent: "center", color: "white" }}>{this.props.title}</div>
            </div>
        )
    }

}